import toufang from '@/static/mbcImg/home/extendManageList/toufang.png';
import shuju from '@/static/mbcImg/home/extendManageList/shuju.png';
import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'gadget',
	data() {
		return {
			toufang: toufang,
			shuju: shuju,
			list:[],
		};
	},
	created() {},
	mounted() {},
	computed: {
		i18n() {
			return this.$t('extendManageList');
		}
	},
	methods: {
		Statistics(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/putIn/760', //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content;
						// this.$store.commit('setMation', this.List); // 更新vuex
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		goToPutInBusiness() {
			console.log('to投放商机');
			if (this.list.rpCompanyAuth == 0) {
				uni.showModal({
					title: '提示',
					content: '您的公司还未认证，无法进行广告投放，快去提交公司认证信息吧～',
					confirmColor: ' #02C2A2',
					confirmText: '去认证',
					cancelText: '暂不认证',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			} else {
				if (uni.getStorageSync('landRegist')) {
					let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
					console.log(landRegistLG.user.id);
					let params = {
						page: String(1),
						userId: 760
					}; // 请求总数居时 参数为空
					uni.showLoading({
						// 展示loading
						title: '加载中'
					});
					uni.request({
						url: this.api2 + '/rest-rp/putIn/selectInstList', //接口地址。
						data: params,
						method: 'POST',
						header: {
							Authorization: 'Bearer ' + landRegistLG.token //将token放到请求头中
						},
						success: response => {
							uni.hideLoading();
							console.log(response.data);
							this.putInBusinessData = response.data.content.list;
							// this.list = response.data.content
							this.$store.commit('setputInBusinessData', this.putInBusinessData); // 更新vuex
							if (response.data.ret == 200) {
								uni.navigateTo({
									url: '/modules/aLittle/putInBusiness/putInBusiness'
								});
							}
						},
						fail: error => {
							uni.hideLoading(); // 隐藏 loading
							uni.showToast({
								title: '网络繁忙，请稍后',
								icon: 'none',
								duration: 1000
							});
							console.log(error, '网络繁忙，请稍后');
						}
					});
				}
			}
		},
		goToDataReport() {
			console.log('to数据报表');
			this.$router.push({
				path: '/dataReport',
				query: {}
			});
		}
	}
};
